<template>
  <div class="header">
    <div class="container">
      <h1 class="logo">
        <router-link :to="$publicUrl+'/'" :style="{color:'#fff'}">
          <img src="https://www.vue-js.com/public/images/vue.png" />
          Vue.js
        </router-link>
      </h1>
      <div class="search">
        <input type="text" />
      </div>
      <ul class="navigation">
        <li>
          <router-link :to="$publicUrl+'/'" :style="{color:'#fff'}">Home</router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}"></router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}">Vue2.0</router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}"></router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}">Api</router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}"></router-link>
        </li>
        <li>
          <router-link to="/" :style="{color:'#fff'}"></router-link>
        </li>
      </ul>
      <router-link v-if="userinfo" :to="this.$publicUrl+'/my/messages'" class="mymessages">
        <span v-if="newMessages" class="message-count">{{newMessages}}</span> 
      </router-link>
      <router-link v-if="userinfo" :to="this.$publicUrl+'/topic/create'" class="postTitle"></router-link>
      <div v-if="!userinfo" class="login">
        <input type="text" v-model="usertoken" />
        <button @click="login"></button>
      </div>
      <div v-else class="logout">
        <img :src="userinfo.avatar_url" />
        <button @click="logout"></button>
      </div>
    </div>
  </div>
</template>

<script>
//      1.cookie    2.localStorage   3.sessionStorage
//local  session    local  session 
//  
// localStorage.setItem('','')   sessionStorage.setItem('','')
//  localStorage.getItem('','')   sessionStorage.getItem('','')
// localStorage.removeItem('')  localStorage.clear()   session yizhi
//  boolean number string
// (f12) application

import axios from "axios";
export default {
  name: "headernav",
  data() {
    return {
      usertoken: "8f8bbbad-f0ff-4f15-8f5f-750f4d34aba0",
      userinfo: null,
      newMessages: null
    };
  },
  methods: {
    login() {
      this.$router.push(this.$publicUrl+"/");
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: this.usertoken
        })
        .then(res => {
          console.log(res.data);
          this.userinfo = res.data;
          localStorage.setItem("token", this.usertoken);
          localStorage.setItem("userId", res.data.id);
          axios
            .get("https://www.vue-js.com/api/v1/message/count", {
              params: {
                accesstoken: localStorage.getItem("token")
              }
            })
            .then(res => {
              console.log(res.data);
              this.newMessages = res.data.data;
            });
        });
    },
    logout() {
      this.$router.push(this.$publicUrl+"/")
      this.userinfo = null;
      localStorage.removeItem("token");
    }
  },
  // computed: {
  //   loggedin() {
  //     return localStorage.getItem("token");
  //   }
  // },
  created() {
    if (localStorage.getItem("token")) {
      axios
        .post("https://www.vue-js.com/api/v1/accesstoken", {
          accesstoken: localStorage.getItem("token")
        })
        .then(res => {
          this.userinfo = res.data;
          axios
            .get("https://www.vue-js.com/api/v1/message/count", {
              params: {
                accesstoken: localStorage.getItem("token")
              }
            })
            .then(res => {
              console.log(res.data);
              this.newMessages = res.data.data;
            });
        });
    }
  }
};
</script>

<style>
.header {
  padding: 10px 0;
  background-color: #1c6132;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h1.logo {
  font-size: 20px;
  color: #fff;
  font-weight: 200;
  width: 120px;
  height: 45px;
}
.header h1.logo img {
  height: 45px;
  max-width: 100%;
  vertical-align: middle;
}
.header .search {
  position: relative;
}
.header .search input {
  width: 206px;
  height: 20px;
  background: #4f9639 url("https://www.vue-js.com/public/images/search.png")
    no-repeat 4px 0;
  padding: 3px 5px 3px 22px;
  color: #666;
  border: 0;
  outline: 0;
  /* margin-top: 2px; */
  transition: all 0.5s;
  /* margin-bottom: 0; */
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
}
.header .search input:hover {
  background-color: #fff;
}
.header ul.navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header ul.navigation li {
  font-size: 13px;
  line-height: 2;
}
.header ul.navigation li > a {
  padding: 10px 15px;
  color: #eee;
}
.header .login,
.header .logout {
  display: flex;
  align-items: center;
}
.logout img,
.login input {
  width: 45px;
  margin-right: 5px;
}
.logout button,
.login button {
  align-self: flex-end;
}
.mymessages span.message-count {
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -o-border-radius: 8px;
  border-radius: 8px;
  padding: 1px 5px;
  background-color: #369219;
  color: #fff;
  margin-right: 0.5em;
}
</style>
